<template>
  <div class="wrapper" id="app">
    <div class="contents-wrapper">
      <page-header />
      <router-view/>
      <page-footer />
    </div>
  </div>
</template>

<script>
import PageHeader from './components/PageHeader'
import PageFooter from './components/PageFooter'
export default {
  name: 'App',
  head: {
    meta: [
      { name: 'viewport', content: 'width=1400' }
    ]
  },
  created () {
    var vh = window.innerHeight
    document.getElementById('app').style.height = vh + 'px'
  },
  components: {
    PageHeader,
    PageFooter
  }
}
</script>

<style lang="scss">
@import './assets/css/reset.css';
.wrapper {
  padding: 20px 0;
  background: #faf2e5;
  position: relative;
  display: flex;
  justify-content: center;
  min-width: 990px;
  max-width: 100%;
  margin: 0 auto;
  min-height: 100vh;
  .contents-wrapper {
    background: #fff;
    box-shadow: 0 8px 10px 0 rgba(0,0,0,0.2);
    min-height: 350px;
    height: 100%;
  }
}
</style>



